import React, { Component } from 'react';
import SaiyanCard from "./components/SaiyanCard";
import Title from "./components/Title";
import Wrapper from "./components/Wrapper";
import saiyans from "./saiyans.json";
import "./App.css";

// function to shuffle array of images
function shuffleImage(array) {
  for (let i = array.length - 1; i > 0; i--) {
    let j = Math.floor(Math.random() * (i+1));
    let temp = array[i];
    array[i] = array[j];
    array[j] = temp
  }
  return array
};


class App extends Component {
  state = {
    saiyans,
    score: 0,
    topScore: 0,
    clickedSaiyans: []
  };

  clickedImage = id => {
    let clickedSaiyans = this.state.clickedSaiyans;
    let score = this.state.score;
    let topScore = this.state.topScore;

    if (clickedSaiyans.indexOf(id) === -1) {
      clickedSaiyans.push(id);
      this.handleIncrement();
      this.saiyanShuffle();
    } else if (this.state.score === 12) {
      alert("You win!")
      this.setState({
        score: 0,
        clickedSaiyans: []
      });
    } else {
      this.setState({
        score: 0,
        clickedSaiyans: []
      });
      alert("Sorry you picked the same fighter twice, try again!")
    }

    if (score > topScore) {
      topScore = score;
      this.setState({ topScore })
    }
  };

  // function to increment 1 point to score
  handleIncrement = () => {
    this.setState({ score: this.state.score + 1 });
  }

  // function to call shuffleImage function for saiyans
  saiyanShuffle = () => {
    this.setState({ saiyans: shuffleImage(saiyans) })
  }


  render() {
    return (    
      <Wrapper>
         <Title 
            score={this.state.score}
            message={this.state.message}
            topScore={this.state.topScore} />
        <div class="container"> 
          <div class="row">
            {this.state.saiyans.map(character => (
              <SaiyanCard
                id={character.id}
                key={character.id}
                name={character.name}
                image={character.image}
                clickedImage={this.clickedImage}
                />
            ))}
            </div>
        </div>
      </Wrapper>
    ); 
  }
}

export default App;